.user-info-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.profile-image {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  margin-bottom: 20px;
}

.user-name {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 10px;
  color: #12022f;
}

.user-designation {
  font-size: 16px;
  margin-bottom: 10px;
  color: #616e7c;
}
