.blog-item {
  display: flex;
  flex-direction: column;
}

.blog-item-heading-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.blog-title {
  font-size: 20px;
  font-weight: bold;
  color: #12022f;
  margin-bottom: 10px;
}

.blog-description {
  color: #616e7c;
  font-size: 16px;
}

.blog-published-date {
  color: #9aa5b1;
  font-weight: bold;
}
